import { Col, Row } from 'antd';
import React from 'react';
import { Link } from 'react-router-dom';
import JoinUsAnimation from '../../AnimationComponents/JoinUsAnimation';
const JoinUs = () => {
  return (
    <section className='join-us'>
      <Row>
        <Col md={8}>
          <div className='animation'>
            <JoinUsAnimation />
          </div>
        </Col>
        <Col md={16}>
          <div className='content'>
            <div>
              <h3>Join Ginnovation Club</h3>
              <h4>Do more than others</h4>
              <Row gutter={20}>
                <Col md={12}>
                  <div className='reason-card'>
                    <h5>1. Practical Implemantaion</h5>
                  </div>
                </Col>
                <Col md={12}>
                  <div className='reason-card'>
                    <h5>1. Practical Implemantaion</h5>
                  </div>
                </Col>
                <Col md={12}>
                  <div className='reason-card'>
                    <h5>1. Practical Implemantaion</h5>
                  </div>
                </Col>
                <Col md={12}>
                  <div className='reason-card'>
                    <h5>1. Practical Implemantaion</h5>
                  </div>
                </Col>
              </Row>
              <Link to='/signup'>
                <button>Register Now</button>
              </Link>
            </div>
          </div>
        </Col>
      </Row>
    </section>
  );
};

export default JoinUs;
